<template>
  <div>
    <el-row>
      <el-col>
        <el-table :data="subjects" style="width: 100%">
        <el-table-column label="科目ID" prop="subjectID"  v-if="show = false"></el-table-column>
        <el-table-column label="科目名称" prop="subjectName"></el-table-column>
        <el-table-column align="right">
          <template slot="header">
            <el-button size="small" type="primary" @click="dialogFormVisible = true">
              <i class="el-icon-circle-plus-outline" />新建科目
            </el-button>
          </template>
          <template slot-scope="scope">
            <el-button size="small" type="info" @click="select(scope.row)" round>
              <i class="el-icon-search" />查看该科题目
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      </el-col>
    </el-row>
    <!-- 新增模态框 -->
    <el-dialog title="新增" :visible.sync="dialogFormVisible">
      <el-form ref="subject" :rules="rules" :model="subject">
        <el-form-item label="科目名称" :label-width="formLabelWidth" prop="subjectName">
          <el-input v-model="subject.subjectName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('subject')">重 置</el-button>
        <el-button type="primary" @click="Post('subject')">确 定</el-button>
      </div>
    </el-dialog>
    <el-row>
      <el-col>
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Qs from "qs";

export default {
  inject: ["reload"],
  data() {
    return {
      dialogFormVisible: false,
      currentPage: 1,
      total: 0,
      subject: {
        subjectName: ''
      },
      subjects: [],
      formLabelWidth: "120px",
      rules: {
        subjectName: [
          { required: true, message: "请输入科目名称", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    Post(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("post对象：" + this.subject);
          var api = "http://localhost:8888/paper/add/subject";
          this.axios
            .post(api, Qs.stringify(this.subject))
            .then(response => {
              console.log(response);
              if (response.data == 1) {
                this.$message({
                  showClose: true,
                  message: "操作成功",
                  type: "success"
                });
                this.dialogFormVisible = false;
                this.reload();
              }
            })
            .catch(error => {
              console.log(error);
              this.$message({
                showClose: true,
                message: "操作失败，请联系维护人员",
                type: "error"
              });
            });
        } else {
          this.$message({
            showClose: true,
            message: "请检查数据是否填写正确！",
            type: "error"
          });
        }
      });
    },
    handleCurrentChange(current) {
      this.currentPage = current;
      var api = "http://localhost:8888/paper/subject/" + current + "/" + 5;
      this.axios
        .get(api)
        .then(response => {
          console.log("分页响应对象：" + response);
          this.subjects = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    select(row) {
      this.$router.push({
        name: "addIssue",
        params: { subjectID: row.subjectID, current: 1, size: 5 }
      });
    },
    getData() {
      var api =
        "http://localhost:8888/paper/subject/" +
        this.$route.params.current +
        "/" +
        this.$route.params.size;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.subjects = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
</style>